<template>
  <div class="about">

      <div style="width: 900px;margin:0 auto;text-align:left">
      <el-button
       size="mini"
       type="success"
       v-if="multipleSelectionFlag"
       @click="popDelete()">批量删除</el-button>
      </div>

      <el-dialog :visible.sync="multiDeleteVisible" title="提示" width="30%">
          <span>确定要删除吗</span>
          <span slot="footer">
              <el-button type="primary" @click="multiDelete">确 定</el-button>
              <el-button @click="multiDeleteVisible = false">取 消</el-button>
          </span>
      </el-dialog>

       <el-dialog :visible.sync="orderVisible" title="提示" width="30%">
          <span>确定下单</span>
          <span slot="footer">
              <el-button type="primary" @click="multiorder">确 定</el-button>
              <el-button @click="orderVisible = false">取 消</el-button>
          </span>
      </el-dialog>

      <el-table
       :data="lists"
       style="width: 900px;margin:0 auto"
       @selection-change="handleSelectionChange">

          <el-table-column
              type="selection"
              width="60">
          </el-table-column>

          <el-table-column
           label="商品"
           width="400"
           prop="PackingId">
              <template slot-scope="scope">
                  <el-popover trigger="hover" placement="top">
                      <p>商铺: {{ scope.row.product.productBrandId}}</p>
                      <p>商品详情: {{ scope.row.product.productSmallUnit }}</p>
                      <div slot="reference" class="name-wrapper">
                          <img :src="static_url+scope.row.product.productPhoto" @click="showDetail(scope.row.product.productId)"  alt="" style="height:50px; width:100px">
                          <span>{{scope.row.product.productName}}/</span>
                          <span>{{scope.row.detailComment}}</span>
                      </div>
                  </el-popover>
              </template>
          </el-table-column>

          <el-table-column
            label="数量"
            width="150px">
            <template slot-scope="scope">
              <div slot="reference" class="name-wrapper">
                <el-input-number @click.native="changenumber(scope.$index, scope.row)" :min=1 size="mini" v-model="scope.row.detailProductCount"></el-input-number>
              </div>
            </template>
          </el-table-column>

          <el-table-column
           label="单价"
           width="100px">
              <template slot-scope="scope">
                  <div slot="reference" class="name-wrapper">
                      {{scope.row.detailProductPrice}}￥
                  </div>
              </template>
          </el-table-column>
      
          <el-table-column
           label="小计"
           width="100px">
              <template slot-scope="scope">
                  <div slot="reference" class="name-wrapper">
                      {{(scope.row.detailProductPrice*scope.row.detailProductCount).toFixed(2)}}￥
                  </div>
              </template>
          </el-table-column>

          <el-table-column 
           label="操作" 
           width:100px>
              <template slot-scope="scope">
                <el-button
                 size="mini"
                 type="danger"
                 @click="DeleteLine(scope.$index, scope.row)"> 删除
                </el-button>
                  <!-- 删除提示框 -->
                <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
                    <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="delVisible = false">取 消</el-button>
                        <el-button type="primary" @click="deleteRow" >确 定</el-button>
                    </span>
                </el-dialog>
              </template>
          </el-table-column>
      </el-table>
      <div style="height:60px;width: 900px;margin:0 auto;text-align:right">
           <div style="width: 150px;float:right;text-align:right">
          <el-button
           style="margin-right:25px"
           size="mini"
           type="success"
           @click="DeleteBulk()">结算
          </el-button>
      </div>
      <div style="whith:700px;float:left">
          <el-button
           style="margin-right:25px"
           size="mini"
           type="success"
           @click="routerBack()">继续购买
          </el-button>
      </div>
      <div style="width: 200px;float:right">
          共计：<span style="color:red;font-size:20px"><em>{{countPrice}}</em></span>￥
          
      </div>
      </div>
      
      
      <div style="width: 900px;margin:0 auto;text-align:left">
          <div style="float:left;width:650px">
              <span>查询订单：</span>
              
          </div>
          <div style="width: 168px;float:left">
              <el-input
                size="mini"
                placeholder="请输入订单ID"
                v-model="input">
                <i slot="prefix" class="el-input__icon el-icon-search"></i>
              </el-input>
          </div>
          <div style="float:left">
              <el-button
              style="margin-right:25px"
              size="mini"
              type="success"
              v-model="input"
              @click="serchOneOrder(input)">搜索
              </el-button>
          </div>
      </div><br><br>
      <hr style= "border:1px dashed #000; margin:0 auto; " width="900px">


      <!-- 历史订单 -->
      
      <el-table
          :data="listOrder"
          style="width: 900px;margin:0 auto">
          <el-table-column
            label="日期"
            width="200px">
            <template slot-scope="scope">
                      <i class="el-icon-time"></i>

              <span style="margin-left: 10px">{{ scope.row.orderDateTime }}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="订单号"
            width="605px">
            <template slot-scope="scope">
              <el-popover  placement="top">
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.orderId }}</el-tag>
                </div>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
             
              <el-button
                size="mini"
                type="danger"
                @click="handlDetail(scope.$index, scope.row),orderDetailCount(scope.$index, scope.row)">查看明细</el-button>
            </template>
          </el-table-column>
        </el-table>
        
        <!-- 详情 -->
        <el-dialog title="订单详情" :visible.sync="dialogTableVisible">
          <el-table  :data="orderdetaillist">
            <el-table-column label="商品名" width="150">
              <template slot-scope="scope">
                  <div slot="reference" class="name-wrapper">
                      {{scope.row.product.productName}}
                  </div>
              </template>
            </el-table-column>
            <el-table-column label="商品数量" width="200">
               <template slot-scope="scope">
                  <div slot="reference" class="name-wrapper">
                      {{scope.row.detailProductCount}}
                  </div>
              </template>
            </el-table-column>
            <el-table-column label="口味">
               <template slot-scope="scope">
                  <div slot="reference" class="name-wrapper">
                      {{scope.row.detailComment}}
                  </div>
              </template>
            </el-table-column>
            <el-table-column label="小计">
               <template slot-scope="scope">
                  <div slot="reference" class="name-wrapper">
                      <span>{{scope.row.detailTotalPrice}}￥</span>
                  </div>
              </template>
            </el-table-column>
          </el-table>
          <div style="text-align:right;width:88%">
               <span style="color:red">共计：<em>{{orderDetailSum}}</em>￥</span>
          </div>
        </el-dialog>
  </div>
</template>

<script>
export default {

    data() {
        return {
          static_url:'http://www.wxhwg.com:8080/mock/static/',
          // 'd42961ed-3649-4f30-aa87-53dc1e3d604f'
          uid: JSON.parse(this.$store.state.loginUser).userId,
          lists:'',//购物车商品实体数组
          listOrder:'',//订单实例
          delVisible:false,//删除提示弹框的状态
          multipleSelection:[],//多选的数据
          detailId:'',//单行删除的ID
          removetr:'',//单行删除的行标
          listID:'',//批量删除的ID
          multipleSelectionFlag:false,//批量按钮是否出现
          multiDeleteVisible:false,//确认删除会话框是否出现
          orderVisible:false,//确认结算会话框是否出现
          value: '0',//查询订单开启依据
          ordertableVisible:'none',//是否显示ordertable
          input:'',//查询订单输入框
          orderdetaillist:'',//订单详情数组
          countPrice:'0',//商品总价
          dialogTableVisible:false,//详情对话框显示
          orderDetailSum:'',
        }
    },

    created(){
        this.init()
        this.serchAllorder();
    },

    methods: {
        
        // 初始化购物车数据
        init(){
          // 判断登录状态
          let user = JSON.parse(this.$store.state.loginUser);
          if(!user){
              var mymessage=confirm("请先登录账号！");
              if(mymessage==true){   
                  this.$router.push({path:'/login'});
              }else{
                  //
              }
              return;
          }

          let self=this
          this.$axios.get("http://www.wxhwg.com:8080/mock/api/detail/qryCartList",{
                  params:{
                    uid:this.uid
                  }
              }).then(response=>{
                  self.countPrice = 0;
                  let result =response.data
                  if(result.status == 200){
                      this.lists=result.data
                      this.lists.forEach(function(item){
                      self.countPrice=(Number(self.countPrice)+Number(item.detailProductPrice*item.detailProductCount)).toFixed(2)
                    })
                  }else{
                    alert("查询失败！");
                  }
              }).catch(error=>{
                 alert(error)
              });
        },

         //得到选择框选中数据
        handleSelectionChange(val) {
            this.multipleSelection = val;
            console.log(this.multipleSelection)
            this.multipleSelectionFlag = true;
            if (this.multipleSelection.length == 0) {   
                this.multipleSelectionFlag = false; // 如不进行判断则勾选完毕后批量删除按钮还是会在
            }
        },

        //订单详情总价
        orderDetailCount(index,row){
            let id=row.orderId
            let sum=0
            this.orderDetailSum
            this.listOrder.forEach(function(item){
              if(item.orderId==id){
                   sum=item.orderTotalPrice
                   return;
              }
            })            
            this.orderDetailSum=sum

        },
        //查看订单详情
        handlDetail(index,row){
             let input=row.orderId
             this.serchOneOrder(input)
        },

        //返回首页继续购买
        routerBack(){
          this.$router.push({path:'/index'});
        },
      
        //移除购物车(一行)
        DeleteLine(index, row) {
            console.log(index, row);
            this.delVisible = true;
            this.detailId=row.detailId;
            this.removetr=index
          
        },
        // 确定删除(一行)
        deleteRow(){
            this.$axios.get("http://www.wxhwg.com:8080/mock/api/detail/cartDel",{
                    params:{
                        detailId:this.detailId
                    }
                }).then(res=>{
                    if(res.data.status=="200"){
                       this.lists.splice(this.removetr,1)
                       this.init();
                    }
                }).catch(error=>{
                    console.log(error);
                    this.$message.error('包装删除失败')
                })
                this.delVisible = false;//关闭删除提示模态框
            },

        //多行删除
        popDelete(){
            this.multiDeleteVisible=true//显示多行删除对话框
        },
            multiDelete(){
              this.multiDeleteVisible = false;
              let checkArr = this.multipleSelection;   // multipleSelection存储了勾选到的数据
              let _params = [];
              let self = this;
              checkArr.forEach(function (item) {     
                _params.push(item.detailId);       // 添加所有需要删除数据的id到一个数组，post提交过去
              });
              _params.forEach(function(item){
                self.$axios.get("http://www.wxhwg.com:8080/mock/api/detail/cartDel",{
                          params:{
                              detailId:item
                          }
                      }).then(res=>{
                          if(res.data.status==200){
                            self.lists.splice(self.removetr,1)
                            this.reload()
                          }
                      }).catch(error=>{
                          console.log(error);
                          this.$message.error('包装删除失败')
                      })
              })
            },

        //查询所有订单号
        serchAllorder(){
           this.$axios.get("http://www.wxhwg.com:8080/mock/api/order/selectHistory",{
                  params:{
                    userId:this.uid
                  }
              }).then(response=>{
                  let result =response.data
                  if(result.status == 200){
                      this.listOrder=result.data
                  }else{
                    alert("查询失败！");
                  }
              }).catch(error=>{

              })
        },

        //查询指定订单详情
        serchOneOrder(byorderId){
           this.$axios.get("http://www.wxhwg.com:8080/mock/api/order/detail",{
                      params:{
                        orderId:byorderId
                      }
                  }).then(response=>{
                      let result =response.data
                      if(result.status == 200){
                          this.orderdetaillist=result.data
                          this.dialogTableVisible=true
                      }else{
                        alert("查询失败！");
                      }
                  }).catch(error=>{

                  })
        },

        //结算
        DeleteBulk(){
           if(this.lists.length == 0){
                this.$message.warning("购物车空空如也~");
                return;
            }
            this.orderVisible=true//显示多行删除对话框


        },
        multiorder(){
            this.orderVisible = false;
            let self = this;
                self.$axios({
                        method:"post",
                        url:"http://www.wxhwg.com:8080/mock/api/order/commit",
                        data:self.$qs.stringify( {
                        orderUserId:self.uid ,
                        orderTotalPrice:self.countPrice,
                        orderComment:null
                    })
                }).then(res=>{
                        if(res.data.status==200){
                          const h = this.$createElement;
                          this.$message({
                              message: h('p', null, [
                              h('span', null, '下单成功！！！'),
                              h('i', { style: 'color: teal' })
                              ])
                              });  
                          self.lists.splice(self.removetr,1)
                          this.init();
                          this.serchAllorder();
                        }
                    }).catch(error=>{
                        console.log(error);
                        // this.$message.error('包装删除失败')
                    })
          },
          showDetail(id){
            this.$router.push({path:'/productdetail',query : { id }})
          }
    }
}
</script>

<style scoped>
    .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 200px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 100%;
  }
</style>